@use '../../../styles/utils/variables';
@use '../../../styles/utils/mixins';

fieldset {
  margin: 0;
  padding: 0;
  border: none;
  display: flex;
  flex-direction: column;
  gap: 6px;

  span.error {
    @include mixins.error();
  }
}

legend {
  @include mixins.label();
  margin-bottom: 6px;
}